import p5 from 'p5';


let databaseUrl = "data/database.json";
let database;


function sketch(data) {
  return (p) => {
    let img, blackout, canvas,  touchStartX, touchStartY, loading = false;
    let height = 300*1.5;
    let width = height * 1.53;
    let noiseLevel = 15 
    let xOffset = 0, yOffset = 0;
    let currentIndex = -1;
    let assetsPath = "data/img/"
    let imagePath = "data/img/prints/"

    function centerCanvas() {
      let centerX = (p.windowWidth - width) / 2;
      let centerY = (p.windowHeight - height) / 2;
      canvas.position(centerX, centerY);
    }

    function loadImageWithDelay(indexChange, delay) {
      loading = true;
      currentIndex = (currentIndex + indexChange + data.length) % data.length;
      setTimeout(() => {
        img = p.loadImage(imagePath + data[currentIndex]["Filename"], () => {
          loading = false; 
        });
      }, delay);
    }

    p.preload = () => {
      //img = p.loadImage('https://upload.wikimedia.org/wikipedia/commons/4/47/PNG_transparency_demonstration_1.png')
      blackout = p.loadImage(assetsPath + "blackout.gif");
      img = p.loadImage(assetsPath + "welcome.gif")
    };
    p.windowResized = () => {
      centerCanvas();
    }
    p.keyPressed = () => {
      if (loading) return;
      if (p.keyCode === p.LEFT_ARROW) {
        loadImageWithDelay(-1, 400);
      } else if (p.keyCode === p.RIGHT_ARROW) {
        loadImageWithDelay(1, 400);
      }
    }

    p.touchStarted = () => {
      touchStartX = p.mouseX;
      touchStartY = p.mouseY;
    };
    p.touchEnded = () => {
      let deltaX = p.mouseX - touchStartX;
      let deltaY = p.mouseY - touchStartY;

      if (Math.abs(deltaX) > Math.abs(deltaY)) {
        // Horizontal swipe
        if (deltaX < 0) {
          // Swipe to the right
          loadImageWithDelay(1, 400);
        } else {
          // Swipe to the left
          loadImageWithDelay(-1, 400);
        }
      }
    };
    p.setup = () => {
      p.frameRate(24); 
      canvas = p.createCanvas(width, height);
      canvas.style('z-index', '2')
      p.pixelDensity(1)
      centerCanvas();
      p.image(img,0,0)
    };
    p.draw = () => {
      if (loading) {
        blackout.resize(0, height);
        p.image(blackout, 0, 0);
      } else {
        img.resize(0, height);
        p.image(img, 0, 0);
      }
    };
  };
}
function computer(x, y, s) {
  return (p) => {
    let canvas
    let img;
    let xOffset = x, yOffset = y, size = s
    

    function centerCanvas() {
      let centerX = (p.windowWidth - img.width + xOffset) / 2;
      let centerY = (p.windowHeight - img.height + yOffset) / 2;
      canvas.position(centerX, centerY);
      canvas.resize(img.width, img.height)
    }

    p.windowResized = () => {
      centerCanvas();
    }


    p.preload = () => {
      img = p.loadImage('data/img/macbookpro.png');
    };

    p.setup = () => {
      canvas = p.createCanvas(img.width, img.height);
      canvas.style('z-index', '1')
    }

    p.draw = () => {
      img.resize(0, size);
      centerCanvas();
      p.fill(255)
      p.image(img,xOffset,yOffset);   
      
    } 
  }
} 

function background() {
  return (p) => {
    let canvas;
    let cols, rows;
    let scl = 40; // scale factor for the grid
    let w = 2000; // width of the grid
    let h = 1600; // height of the grid
    let flying = 0; // variable to simulate flying through the terrain

    p.setup = () => {
      canvas = p.createCanvas(p.windowWidth, p.windowHeight, p.WEBGL);
      cols = w / scl;
      rows = h / scl;
      canvas.style('z-index', '0');
      p.frameRate(20);
    }

    p.windowResized = () => {
      p.resizeCanvas(p.windowWidth, p.windowHeight);
      cols = w / scl;
      rows = h / scl;
    }

    p.draw = () => {
      p.background(0);
      flying -= 0.01; // adjust this value to change the speed of the flying effect

      let yoff = flying;
      for (let y = 0; y < rows; y++) {
        let xoff = 0;
        for (let x = 0; x < cols; x++) {
          let z = p.map(p.noise(xoff + p.mouseX * 0.001, yoff + p.mouseY * 0.001), 0, 1, -100, 100); // map the noise value to the z position
          p.push();
          p.translate(x * scl - w / 2, y * scl - h / 2, z); // translate to the grid position and the z position
          p.box(scl, scl, scl*10); // draw a box
          p.pop();
          xoff += 0.2; // adjust this value to change the noise scale
        }
        yoff += 0.2; // adjust this value to change the noise scale
      }
    }
  }
}

let mySketch = new p5(background());

let comp = new p5(computer(16, 14, 377*1.5));
